import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue"
import otherLogin from '../../components/home/other-login.vue'
export default {
	components:{
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			disabled:true,
			status:false,
			username:'',
			password:'',
			phone:'',
			checknum:'',
			codetiem:0
		}
	},
	watch:{
		username(){
			this.onbtn()
		},
		password(){
			this.onbtn()
		},
		phone(){
			this.onbtn()
		},
		checknum(){
			this.onbtn()
		}
	},
	
	onLoad() {
		console.log(this.user.userinfo)
	},
	methods: {
		//验证手机号码
		isphone(){
			let reg=/^1[3456789]\d{9}$/
			return reg.test(this.phone)
		},
		//获取验证码
		async getchangenum(){
			if(this.codetiem>0){
				uni.showToast({
					title:'不能重复获取',
					icon:"none"
				})
				return
			}
			if(!this.isphone()){
				uni.showToast({
					title:"请输入正确的手机号码",
					icon:"none"
				})
				return
			}
			//发送请求
			let [err,res]=await this.$http.post('/user/sendcode',{phone:this.phone})
			this.$http.errorcheck(err,res)
			
			uni.showToast({
				title:JSON.stringify(res.data.msg),
				icon:"none"
			})
			if(!res.data.errCode)return
			//发送成功,开起倒计时
			
			uni.showToast({
				title:res.data.data.msg,
				icon:"none"
			})
			this.codetiem=60
			let timer=setInterval(()=>{
				this.codetiem--
				if(this.codetiem<1){
					clearInterval(timer)
					this.codetiem=0
				}
			},1000)
		},
		//初始化表单
		initinput(){
			this.username=''
			this.password=''
			this.phone=''
			this.checknum=''
		},
		//改变按钮的状态13656457563 6122
		onbtn(){
			if(this.username&& this.password || this.phone && this.checknum){
				this.disabled=false
			}else{
				this.disabled=true
			}
		},
		changestatus(){
			this.initinput()
			this.status=!this.status
		},
		back(){
			uni.navigateBack({
				delta: 1
			});
			
		},
		submit(){
			//提交时判断是哪种登录的方式
			//账号密码登录
			if(!this.status){
				return this.user.login({
					url:'/user/login',
					data:{
						username:this.username,
						password:this.password
					}
				})
			} 
			if(!this.isphone()){//验证码登录
				return
			}
			this.user.login({
				url:'/user/phonelogin',
				data:{
					phone:this.phone,
					code:this.checknum
				}
			})
		}
		
	},
	onLoad(e) {
		
	}
}
